.gallery {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 50px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    margin: 0 auto;
    margin-top: 77px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 auto;
    margin-top: 88px;
  }
}

.gallery__image {
  @media (min-width: @tablet-width) {
    width: 100%;
    min-height: 509px;
  }

  @media (min-width: @desktop-width) {
    min-height: 506px;
  }
}
