.slider__toggle {
  padding: 0;
  margin: 0 7px;
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  background: #bec2c7;
  border-radius: 20px;
  cursor: pointer;
  border: none;
  outline: none;

  &:hover {
    background: #7e868f;
  }

  &:active {
    background: #e9ebec;
  }

  &--active {
    width: 12px;
    height: 12px;
    border: 3px solid #000000;
    background: #ffffff;

    &:hover,
    &:active {
      background: #ffffff;
    }
  }
}

.slider__toggles {
  display: block;

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.slider__toggle-arrow {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
    padding: 0;
    position: absolute;
    top: 25px;
    width: 22px;
    height: 41px;
    cursor: pointer;
    border: none;
    outline: none;
    background-color: transparent;
    background-repeat: no-repeat;
    opacity: 0.6;

    &:hover {
      opacity: 1;
    }

    &:active {
      opacity: 0.2;
    }

    &--left {
      left: 0;
      top: 47px;
    }

    &--right {
      right: 0;
      top: 47px;
    }
  }
}
